import React, { useState } from "react";
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CFormFeedback,
} from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { cilLockLocked, cilUser } from "@coreui/icons";
import { Link } from "react-router-dom";

import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";

import Message from "../../../components/Message";

import { registerUser } from "../../../provider/userFetch";

const schemaUser = yup.object().shape({
  firstName: yup.string().required("firstName is required"),
  lastName: yup.string().required("lastName is required"),
  email: yup
    .string()
    .email("email must email valid")
    .required("email is required"),
  password: yup.string().required("password are required"),
  confirmPassword: yup
    .string()
    .oneOf([yup.ref("password"), null], "Passwords must match"),
});

const Register = () => {
  /** State */
  const [message, setMessage] = useState(null);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState(false);

  /** Form Hook */
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting, isValid },
    resetField,
  } = useForm({
    resolver: yupResolver(schemaUser),
    mode: "onTouched",
  });

  const onSubmit = async (data) => {
    let resp = null;

    if (isValid) {
      /** reste */
      setSuccess(false);
      setError(false);
      resp = await registerUser(data);
    }

    if (resp?.status) {
      /** Reset */
      resetField("firstName");
      resetField("lastName");
      resetField("email");
      resetField("password");
      resetField("confirmPassword");

      setSuccess(true);
    } else {
      setError(true);
      setMessage(resp?.error);
    }
  };

  return (
    <div className="bg-light min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center mt-3">
          <CCol md={9} lg={7} xl={6}>
            {error && <Message> {message}</Message>}
            {success && (
              <Message>
                {`Success register`}

                <Link to="/login" className="my-2">
                  <CButton
                    color="primary"
                    className="mt-3 my-2"
                    active
                    tabIndex={-1}
                  >
                    Login Now!
                  </CButton>
                </Link>
              </Message>
            )}
          </CCol>
          <CCol md={9} lg={7} xl={6}>
            <CCard className="mx-4">
              <CCardBody className="p-4">
                <CForm noValidate onSubmit={handleSubmit(onSubmit)}>
                  <h1>Register</h1>
                  <p className="text-medium-emphasis">Create your account</p>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilUser} />
                    </CInputGroupText>
                    <CFormInput
                      invalid={errors.firstName?.message ? true : false}
                      placeholder="firstName"
                      autoComplete="firstName"
                      {...register("firstName")}
                    />
                    <CFormFeedback invalid>
                      {errors.firstName?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilUser} />
                    </CInputGroupText>
                    <CFormInput
                      invalid={errors.lastName?.message ? true : false}
                      placeholder="lastName"
                      autoComplete="lastName"
                      {...register("lastName")}
                    />
                    <CFormFeedback invalid>
                      {errors.lastName?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>@</CInputGroupText>
                    <CFormInput
                      invalid={errors.email?.message ? true : false}
                      placeholder="Email"
                      autoComplete="email"
                      {...register("email")}
                    />
                    <CFormFeedback invalid>
                      {errors.email?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-3">
                    <CInputGroupText>
                      <CIcon icon={cilLockLocked} />
                    </CInputGroupText>
                    <CFormInput
                      invalid={errors.password?.message ? true : false}
                      type="password"
                      placeholder="Password"
                      autoComplete="new-password"
                      {...register("password")}
                    />
                    <CFormFeedback invalid>
                      {errors.password?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <CInputGroup className="mb-4">
                    <CInputGroupText>
                      <CIcon icon={cilLockLocked} />
                    </CInputGroupText>
                    <CFormInput
                      invalid={errors.confirmPassword?.message ? true : false}
                      type="password"
                      placeholder="Repeat password"
                      autoComplete="new-password"
                      {...register("confirmPassword")}
                    />
                    <CFormFeedback invalid>
                      {errors.confirmPassword?.message}
                    </CFormFeedback>
                  </CInputGroup>
                  <div className="d-grid">
                    <CButton
                      className={(isSubmitting) ? `disabled` : ""}
                      color="success"
                      type="submit"
                    >
                      Create Account
                    </CButton>
                  </div>
                </CForm>
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};

export default Register;
